// import 'jweixin'
(function(window, $) {
    //
    //1. mzlc
    //2. 
    //3. 
    var WeixinShareUtil = {
        wxConfig: {
            debug: false, // ,apialertpclogpc
            appId: '', // 
            timestamp: 0, // 
            nonceStr: '', // 
            signature: '', // 1
            jsApiList: ['onMenuShareTimeline',
                    'onMenuShareAppMessage',
                    'onMenuShareQQ',
                    'onMenuShareWeibo',
                    'onMenuShareQZone',
                    'hideMenuItems',
                    'showMenuItems'
                ] // JSJS2
        },

        setWxConfig: function(obj) {
            var t = this;
            $.ajax({
                url: '//api.mizlicai.com/system/wxconfig.json',
                type: 'GET',
                success: function(data) {
                    t.wxConfig.appId = data.appid;
                    t.wxConfig.timestamp = data.timestamp;
                    t.wxConfig.nonceStr = data.noncestr;
                    t.wxConfig.signature = data.signature;
                    wx.config(t.wxConfig);
                    t.shareConfig(obj);
                }
            });
        },

        shareConfig: function(obj) {
            var t = this;
            var _slink = obj.sharelineLink;
            wx.ready(function(data) {
                 wx.showMenuItems({
                    menuList: [
                        "menuItem:share:appMessage",
                        "menuItem:share:timeline",
                        "menuItem:share:qq"
                        ]
                    });
                //
                wx.onMenuShareTimeline({
                    title: obj.sharedesc, // 
                    link: obj.sharelineLink, // 
                    imgUrl: obj.shareimgUrl, // 
                    success: function() {
                        // 

                    },
                    cancel: function() {
                        // 
                    }
                });
                //
                wx.onMenuShareAppMessage({
                    title: obj.sharetext, // 
                    desc: obj.sharedesc, // 
                    link: _slink, // 
                    imgUrl: obj.shareimgUrl, // 
                    type: '', // ,musicvideolinklink
                    dataUrl: '', // typemusicvideo
                    success: function(resp) {
                        // 
                    },
                    cancel: function() {
                        // 
                    }
                });
                //qq
                wx.onMenuShareQQ({
                    title: obj.sharetext, // 
                    desc: obj.sharedesc, // 
                    link: obj.sharelineLink, // 
                    imgUrl: obj.shareimgUrl, // 
                    success: function() {
                        // 
                    },
                    cancel: function() {
                        // 
                    }
                });
                //
                wx.onMenuShareWeibo({
                    title: obj.sharetext, // 
                    desc: obj.sharedesc, // 
                    link: obj.sharelineLink, // 
                    imgUrl: obj.shareimgUrl, // 
                    success: function() {
                        //
                    },
                    cancel: function() {
                        // 
                    }
                });
                //qq
                wx.onMenuShareQZone({
                    title: obj.sharetext, // 
                    desc: obj.sharedesc, // 
                    link: obj.sharelineLink, // 
                    imgUrl: obj.shareimgUrl, // 
                    success: function() {
                        //  
                    },
                    cancel: function() {
                        // 
                    }
                });
            });
        },
        shareConfighide: function(obj) {
            var t = this;
            var _slink = obj.sharelineLink;
            wx.ready(function(data) {
                //
                wx.onMenuShareTimeline({
                    title: obj.sharetext, // 
                    link: obj.sharelineLink, // 
                    imgUrl: obj.shareimgUrl, // 
                    success: function() {
                        // 

                    },
                    cancel: function() {
                        // 
                    }
                });
                //
                wx.onMenuShareAppMessage({
                    title: obj.sharetext, // 
                    desc: obj.sharedesc, // 
                    link: _slink, // 
                    imgUrl: obj.shareimgUrl, // 
                    type: '', // ,musicvideolinklink
                    dataUrl: '', // typemusicvideo
                    success: function(resp) {
                        // 
                    },
                    cancel: function() {
                        // 
                    }
                });
                //qq
                wx.onMenuShareQQ({
                    title: obj.sharetext, // 
                    desc: obj.sharedesc, // 
                    link: obj.sharelineLink, // 
                    imgUrl: obj.shareimgUrl, // 
                    success: function() {
                        // 
                    },
                    cancel: function() {
                        // 
                    }
                });
                //
                wx.onMenuShareWeibo({
                    title: obj.sharetext, // 
                    desc: obj.sharedesc, // 
                    link: obj.sharelineLink, // 
                    imgUrl: obj.shareimgUrl, // 
                    success: function() {
                        //
                    },
                    cancel: function() {
                        // 
                    }
                });
                //qq
                wx.onMenuShareQZone({
                    title: obj.sharetext, // 
                    desc: obj.sharedesc, // 
                    link: obj.sharelineLink, // 
                    imgUrl: obj.shareimgUrl, // 
                    success: function() {
                        //  
                    },
                    cancel: function() {
                        // 
                    }
                });

                wx.hideMenuItems({
                    menuList:[
                        "menuItem:share:appMessage",
                        "menuItem:share:timeline",
                        "menuItem:share:qq"
                    ]
                 });
            });
        }
    };

    function getUrlParam(key) {
        var search = location.search;
        var arr = !search ? [] : location.search.substr(1).split('&');
        var param = {};
        for (var i = 0, l = arr.length; i < l; i++) {
            var kv = arr[i].split('=');
            param[kv[0]] = kv[1];
        }
        return key ? (param[key] || '') : param;
    }

    window.MizShare = {

        shareObj: {},

        config: function(obj) {
            this.shareObj = obj;
            WeixinShareUtil.setWxConfig(obj);
        },

        confighide: function(obj) {
            this.shareObj = obj;
            WeixinShareUtil.setWxConfig(obj);
            WeixinShareUtil.shareConfighide(obj);
        },

        share: function() {
            var t = this;
            //mizApp
            var isApp = getUrlParam('os').toString() || sessionStorage.getItem("osKey");
            var ua = navigator.userAgent.toLowerCase();
            if (isApp.toLowerCase().indexOf('ios') != -1 || isApp.toLowerCase().indexOf('android') != -1) {
                var shareObj = {
                    desc: t.shareObj.sharedesc,
                    text: t.shareObj.sharetext,
                    lineLink: t.shareObj.sharelineLink,
                    imgUrl: t.shareObj.shareimgUrl
                };
                location.href = "share:test?x=" + encodeURIComponent(JSON.stringify(shareObj));
            } else if (ua.match(/MicroMessenger/i) == "micromessenger") {
                    var fuceng = $('<div class=\"share-fuceng\" style=\"position:fixed;opacity:0.7;background-color:#000;z-index:99999;left:0;top:0;bottom:0;right:0;width:100%;height:100%\"><img src=\"http://miz-image.b0.upaiyun.com/zuimifen/wx-share.png\" width=\"100%\"/></div>');
                    $(document.body).append(fuceng);
                    $('.share-fuceng').on('click', function() {
                        $('.share-fuceng').remove();
                        $(document).unbind('click');
                    });
                }else {
                    alert('');
                }
        }
    };
})(window, jQuery)



